* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box !important;
    box-sizing: border-box !important;
}

body {
    background: #34393f;
}

header {
    padding: 25px 15px;

    .container-fluid {
        .logo {
            padding-top: 10px;

            .logo_div {
                img {
                    width: 150px;
                    height: 35px;
                }
            }
        }
    }
}

main {
    display: flex;
    justify-content: center;
    align-items: center;

    .main_div {
        display: flex;
        justify-content: center;
        align-items: center;

        .main_text {
            display: flex;
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            text-align: left;
            padding-left: 100px;
            

            // h1 {
            //     width: 100%;
            //     font-size: 40px;
            //     color: #e09f28;
            //     font-family: 'Nunito', sans-serif;
            //     margin-bottom: 30px;
            // }

            .sign-wrap-1 {

                text-align: center;
                margin-bottom: 30px;
            }
            .sign-wrap-1 .sign_word {
                font-size: 60px;
                font-weight: bold;
                line-height: 50px;
                color: #FFF;
                font-family: 'Tourney', cursive;
                text-transform: uppercase;
                animation: neon-1 .6s ease-in-out infinite alternate;
            }
            @keyframes neon-1 {
                from {
                    text-shadow: 0 0 6px rgba(202, 228, 225, 0.92), 0 0 30px rgba(255, 255, 255, 0.34), 0 0 12px rgba(255, 255, 255, 0.52), 0 0 21px rgba(191, 226, 255, 0.92), 0 0 34px rgba(191, 226, 255, 0.78), 0 0 54px rgba(191, 226, 255, 0.92);
                }
                to {
                    text-shadow: 0 0 6px rgba(202, 228, 225, 0.98), 0 0 30px rgba(221, 206, 0, 0.42), 0 0 12px rgba(192, 196, 0, 0.58), 0 0 22px rgba(191, 226, 255, 0.84), 0 0 38px rgba(191, 226, 255, 0.88), 0 0 60px #FFF;
                }
            }

            p {
                width: 100%;
                color: white;
                font-family: 'Nunito', sans-serif;
                font-weight: 400;
                font-size: 16px;
                margin-bottom: 30px;
            }

            .social_media {
                display: flex;
                justify-content: flex-start;
                align-items: flex-start;

                .soc_icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 60px;
                    height: 60px;
                    background: linear-gradient(145deg, #383d43, #2f3339);
                    box-shadow: 5px 5px 10px #151719,
                        -5px -5px 10px #535b65;
                    border-radius: 50%;
                    margin-right: 20px;

                    img {
                        width: 30px;
                        height: 30px;
                    }
                }

                .soc_icon:hover {
                    background: #34393f;
                    box-shadow: inset 5px 5px 10px #151719,
                        inset -5px -5px 10px #535b65;
                }

                .soc_icon:first-child {
                    padding-right: 5px;
                    padding-top: 2px;
                }
            }
        }

        .main_gif {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;

            a {
                text-align: center;
                padding: 15px 25px;
                display: flex;
                justify-content: center;
                align-items: center;
                text-decoration: none;
                font-size: 20px;
                color: #ffffff;
                font-family: 'Nunito', sans-serif;
                transition: 1s;
                background: #34393f;
                background: linear-gradient(145deg, #383d43, #2f3339);
                box-shadow: 5px 5px 20px #151719,
                    -5px -5px 20px #535b65;

                span {
                    color: #e09f28;
                    margin-left: 10px;
                    padding-top: 2px;
                }
            }

            a:hover {
                margin-left: 20px;
                background: #34393f;
                box-shadow: inset 5px 5px 20px #151719,
                    inset -5px -5px 20px #535b65;
                color: #e09f28;

                span {
                    color: white;
                }
            }
        }
    }
}


